<template>
    <div>
        <el-card v-show="!show_add_board">
            
            <el-row>
                <el-col :span="8">
                    <el-input placeholder='' v-model="id_for_seek">
                        <el-button slot="append" icon="el-icon-search" 
                            @click="seekbyid"
                        ></el-button>
                    </el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type='primary' @click="get_cats_lists"></el-button>
                </el-col>
            </el-row>
            <el-table
                v-if="goodlist"
                :data='goodlist'
            >
                <el-table-column 
                    prop='goods_id' label='#'
                />
                <el-table-column 
                    prop='goods_name' label=''
                />
                <el-table-column 
                    prop='goods_price' label='()'
                />
                <el-table-column
                    prop='goods_weight' label=''
                />
                <el-table-column
                    prop='add_time' label=''
                >
                    <template slot-scope="key_obj">
                        <div >{{ key_obj.row.add_time | changeformat }}</div>
                        
                    </template>
                </el-table-column>
                <el-table-column prop='option' label="" >
                    <div slot-scope="scoped">
                        <el-button type='primary' icon="el-icon-edit" @click="run_editor(scoped.row,'edit')"></el-button>
                        <el-button type='danger' icon='el-icon-delete' @click="run_editor(scoped.row,'del')"></el-button>
                    </div>
                </el-table-column>
            </el-table>
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="current_page"
                :page-sizes="page_sizes"
                :page-size="page_size"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </el-card>
        <!-- -->
        <el-card 
            v-show="show_add_board"
            
           >
            <el-alert
                title=''
                center
                show-icon
                :closable='false'
            />
            <!--  -->
            <el-steps 
                align-center
                :active='(a.step_index-0)'
            >
                <el-step 
                    v-for='(m,i) in a.steps' :key='i'
                    :title="m.step_name"
                />
                <el-step title='' />
            </el-steps>
            <el-form>
                <el-tabs 
                    tabPosition='left'
                    v-model="a.step_index"
                    :before-leave='before_leave_this_tab'
                >
                    <el-tab-pane 
                        v-for='(m,i) in a.steps' :key='i'
                        :label="m.step_name"
                        :name='m.index'

                    >
                        <div v-if="m.index=='0'">
                            <el-form-item label=''>
                                <el-input
                                    v-model="a.name"
                                ></el-input>
                            </el-form-item>
                            <el-form-item  label=''>
                                <el-input
                                    v-model="a.price"
                                ></el-input>
                            </el-form-item>
                            <el-form-item  label=''>
                                <el-input
                                    v-model="a.weight"
                                ></el-input>
                            </el-form-item>
                            <el-form-item  label=''>
                                <el-input
                                    v-model="a.number"
                                ></el-input>
                            </el-form-item>
                            <el-form-item  label=''
                                v-if="catlist"
                            >
                                <el-cascader
                                    v-model='a.cat'
                                    :options='catlist'
                                    :props="{ 
                                        expandTrigger: 'hover',
                                        label:'cat_name',
                                        value:'cat_id'
                                    }"
                                >

                                </el-cascader>
                            </el-form-item>
                        </div>
                        <!--  -->
                        <div v-if="m.index=='1'">
                            <el-form-item 
                                v-for='(fm,fi) in attr_data' :key='fi'
                                :label='fm.attr_name'
                            >
                                <el-checkbox v-for='(sf,si) in fm.attr_vals.split(",")' :key='si'>
                                    {{ sf }}
                                </el-checkbox>
                            </el-form-item>
                        </div>
                        <!--  -->
                        <div v-if="m.index=='2'">
                            <el-form-item 
                                v-for='(fm,fi) in attr_data' :key='fi'
                                :label='fm.attr_name'
                            >
                                <el-checkbox v-for='(sf,si) in fm.attr_vals.split(",")' :key='si'>
                                    {{ sf }}
                                </el-checkbox>
                            </el-form-item>
                        </div>
                        <!--  -->
                        <div v-if="m.index=='3'">
                            <el-form-item :label='m.step_name'>
                                <el-upload
                                     class="upload-demo"
                                    action="https://jsonplaceholder.typicode.com/posts/"
                                    multiple
                                    :limit="3"
                                >
                                    <el-button size="small" type="primary"></el-button>
                                </el-upload>
                            </el-form-item>
                        </div>
                        <!--  -->
                        <div v-if="m.index=='4'">
                            <el-form-item :label='m.step_name'>
                                <quill-editor
                                    v-model="a.intr"
                                    :options='editorOption'
                                ></quill-editor>
                            </el-form-item>
                            <div >
                                <el-button @click="show_add_board=false"></el-button>
                                <el-button type='primary' @click="add_sure"></el-button>
                            </div>
                        </div>
                    </el-tab-pane>
                </el-tabs>
            </el-form>
        </el-card>
        <!--   -->
        <el-dialog title=""
            :visible='show_edit_board'
        >
            <el-form v-if="r">
                <el-form-item label=''>
                    <el-input
                        v-model="r.goods_name"
                    ></el-input>
                </el-form-item>
                <el-form-item  label=''>
                    <el-input
                        v-model="r.goods_price"
                    ></el-input>
                </el-form-item>
                <el-form-item  label=''>
                    <el-input
                        v-model="r.goods_weight"
                    ></el-input>
                </el-form-item>
                <el-form-item  label=''>
                    <el-input
                        v-model="r.goods_number"
                    ></el-input>
                </el-form-item>
                <el-form-item  label=''
                >
                    <el-cascader
                        v-model='a.cat'
                        :options='catlist'
                        :props="{ 
                            expandTrigger: 'hover',
                            label:'cat_name',
                            value:'cat_id'
                        }"
                    >

                    </el-cascader>
                </el-form-item>
            </el-form>
            <div slot="footer">
                <el-button @click="show_edit_board=false"></el-button>
                <el-button type='primary' ></el-button>
            </div>
        </el-dialog>
        <!--  -->
        <el-dialog title=""
            :visible='show_del_board'
        >
            <div slot="footer">
                <el-button @click="show_del_board=false"></el-button>
                <el-button type='primary' @click="del_sure(r)" ></el-button>
            </div>
        </el-dialog>
        
    </div>
</template>


<script>
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import { quillEditor } from 'vue-quill-editor'
export default {
    name:'productlist',
    data() {
        return {
             content: '',
            editorOption: {
               placeholder: ''
             },
            // 
            id_for_seek:'',
            // 
            goodlist:null,
            // 
            catlist:null,
            // 
            current_page:1,
            page_sizes:[5,10,25,50,100],
            page_size:5,
            total:3,
            // 
            r:null,
            // 
            show_add_board:false,
            //  
            show_edit_board:false,
            //  
            show_del_board:false,
            // 
            a:{
                steps:[
                    {index:'0',step_name:''},
                    {index:'1',step_name:''},
                    {index:'2',step_name:''},
                    {index:'3',step_name:''},
                    {index:'4',step_name:''}
                ],
                step_index:0,
                name:'',
                price:'',
                number:'',
                weight:'',
                cat:[],
                intr:'',
                pics:'',
                attrs:'',
            },
            step_on_off_one:false,
            attr_data:null,
        };
    },
    created(){

    },
    mounted(){
        this.oninit()
    },
    //props:{},//[]
    methods:{
        // 
        async seekbyid(){
            if(!this.id_for_seek.trim()){
                this.$message('')
                return false
            }else{
                // 
                let {data:res}=await this.$http.get(
                    `/goods`,{
                        params:{
                            query:this.id_for_seek,
                            pagenum:this.current_page,
                            pagesize:this.page_size,
                        }
                    }
                )
                console.log(res)
                if(res.data){
                    this.goodlist=res.data.goods
                    console.log('',this.goodlist)
                    this.total=res.data.total
                }else{
                    this.$message(res.meta.msg)
                }
            }
        },
        // tabs 
        before_leave_this_tab(a,oa){
            console.log(a,oa)
            console.log(this.step_on_off_one)
            if(a>=1&&!this.step_on_off_one){
                this.$set(this.a,'step_index','0')
                this.$message('')
                return false;
            }else{
                if(a==1){
                    this.getstyleinfo('many')
                }else if(a==2){
                    this.getstyleinfo('only')
                }
                return true;
            }
        },
        async getstyleinfo(str){
            let {data:res}=await this.$http.get(
                `/categories/${this.a.cat[2]}}/attributes`,{params:{
                    sel:str
                }}
            )
            console.log('',res)
            if(res.data){
                this.attr_data=res.data
                console.log('',this.attr_data)
            }
        },
        // 
        handleSizeChange(val) {
            console.log(` ${val} `);
            this.page_size=val
            this.oninit()
        },
        // 
        handleCurrentChange(val) {
            console.log(`: ${val}`);
            this.current_page=val
            this.oninit()
        },
        // 
        async get_cats_lists(){
            let {data:res}=await this.$http.get(
                `/categories`,{params:{
                    type:3
                }}
            )
            console.log('',res)
            if(res.data){
                this.catlist=res.data
                console.log('',this.catlist)
                this.show_add_board=true
            }
        },
        async oninit(){
            let {data:res}=await this.$http.get(
                `/goods`,{params:{
                    pagenum:this.current_page,
                    pagesize:this.page_size,
                    query:''
                }}
            )
            console.log('',res)
            this.goodlist=res.data.goods
            console.log('',this.goodlist)
            this.total=res.data.total
        },
        run_editor(r,k){
            this.r=r
            switch (k) {
                case 'del':
                    this.show_del_board=true
                break;
            }
        },
        async add_sure(){
            let ta=this.a
            let {data:res}=await this.$http.post(
                `/goods`,{
                    goods_name:ta.name,
                    goods_cat:ta.cat,
                    goods_price:ta.price,
                    goods_number:ta.number,
                    goods_weight:ta.weight,

                    goods_introduce:ta.intr,
                    pics:ta.pics,
                    attrs:ta.attrs,
                }
            )
            console.log('',res)
            this.show_add_board=false
            this.oninit()
        },
        async edit_sure(r){
            console.log(r)
            let {data:res}=await this.$http.put(
                `/goods/${r.goods_id}`,{
                    goods_name:'',
                    goods_cat:'',
                    goods_price:'',
                    goods_number:'',
                    goods_weight:'',

                    goods_introduce:'',
                    pics:'',
                    attrs:'',
                }
            )
            console.log('',res)
        },
        async del_sure(r){
            let {data:res}=await this.$http.delete(
                `/goods/${r.goods_id}`,{}
            )
            console.log(res)
            this.show_del_board=false
            this.oninit()

        }
    },
    computed:{
        
    },
    watch:{
        a:{
            handler(v){
                let boo=(this.a.name && 
                    this.a.price && 
                    this.a.weight && 
                    this.a.number &&
                    this.a.cat[0]);
                    console.log('',boo)
                this.step_on_off_one=boo
            },
            deep:true,
        }
    },
    filters:{
        
        changeformat(v){
            let t=new Date(v+'000'-0)
            return t.toLocaleDateString()+' '+t.toLocaleTimeString()
        }
    },
    directives:{

    },
    components:{
        quillEditor,
    },
};

</script>

